	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Monday Monday","date":"24 Jan 2000","slug":"/2000-01-24-monday-monday/","tags":"Y2000"},"html":"<p>New Page <a href=\"http://shanson.editthispage.com/discuss/msgReader$111\">Pictures of Christopher</a>: a list of links for those who can't get enough</p>\n<p>John VanDyk, <a href=\"http://vfih.editthispage.com/2000/01/23\">Those dumb farmers</a>: In school, you had the townies and the farm kids. The townies may have had the edge when it came to spelling or math, but the farm kids had the claim to practicality....on the whole, the farm kids had more common sense. <i>Sounds like where I grew up. I was a town kid.</i></p>\n<p>Christopher's big test today was a visit to Mama's office. It sounds like he won them all over. But as I came home from work, she was on the phone with one of her colleagues talking company politics. <i>Her heart's at home, but her head's still in it!</i></p>"},"next":{"fields":{"title":"Sunday Walk","date":"23 Jan 2000","slug":"/2000-01-23-sunday-walk/"}},"previous":{"fields":{"title":"A World Full of Colors","date":"25 Jan 2000","slug":"/2000-01-25-a-world-full-of-colors/"}}},{"node":{"fields":{"title":"Sunday Walk","date":"23 Jan 2000","slug":"/2000-01-23-sunday-walk/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/fph0123.jpg\" height=\"300\" width=\"400\" border=\"0\" alt=\"fph0123.jpg: \" /></p>\n<p>After a wet and sloppy snow day yesterday, it turned cold and clear last night. We bundled up Christopher and went for <a href=\"http://shanson.editthispage.com/stories/storyReader$109\">a walk</a>.</p>\n<p>Christopher felt right at home at the party last night. We stayed over an hour. When we got home, Mama and I were went straight to bed, but Christopher was ready for more! (Note for our to-do list: teach Christopher the difference between day and night.)</p>"},"next":{"fields":{"title":"Christopher Goes Partying","date":"22 Jan 2000","slug":"/2000-01-22-christopher-goes-partying/"}},"previous":{"fields":{"title":"Monday Monday","date":"24 Jan 2000","slug":"/2000-01-24-monday-monday/"}}},{"node":{"fields":{"title":"Christopher Goes Partying","date":"22 Jan 2000","slug":"/2000-01-22-christopher-goes-partying/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/crh0122.jpg\" height=\"400\" width=\"300\" border=\"0\" alt=\"crh0122.jpg: \" /></p>\n<p>Note the wardrobe composed in primary colors so that color-blind Papa can recognize his son's clothes.</p>\n<p>Christopher is going to his first party this evening. Anke from work moved out of the city to the country near us last November, and is having a combination birthday/housewarming party. Mama said that Christopher travelled so well yesterday (visiting and shopping), that we could put in a brief appearance</p>\n<p>Starting today, we have something at home that folks in the US take for granted: flatrate Internet access. Up to now we've always had to pay for the local phone call, 3 to 6 pfennigs per minute. Now there are flatrates available nationwide for around DM 150 a month.</p>"},"next":{"fields":{"title":"Call Me Mr. Pacifier","date":"20 Jan 2000","slug":"/2000-01-20-call-me-mr-pacifier/"}},"previous":{"fields":{"title":"Sunday Walk","date":"23 Jan 2000","slug":"/2000-01-23-sunday-walk/"}}}],"pathPrefix":"","first":false,"last":false,"index":1087,"pageCount":1101}}

